import { ClientSDK, WebsiteChannel } from './types';

export const createWebsiteChannel = ({
  clientSdk,
  applicationId,
}: {
  clientSdk?: ClientSDK;
  applicationId: string;
}): WebsiteChannel => {
  return {
    invoke: async ({ namespace, method, args }) => {
      if (!clientSdk) {
        throw new Error(
          'Wix Site SDK only works in a Wix site environment. Learn more: https://dev.wix.com/docs/sdk/host-modules/site/introduction',
        );
      }

      return clientSdk.invoke({
        namespace,
        method,
        args,
        applicationId,
        accessToken: 'accessToken',
      });
    },
    getAccessToken: () => {
      throw new Error('Not implemented');
    },
    observeState: () => ({
      disconnect: () => {},
    }),
  };
};
